

.loginContainer{
  margin-top: 5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;

}
.loginBtn {
  background-color: #caf6f9;
  border: 2px solid #062f70;
  font-weight: bold;
  color: #232323;

  border-radius: 5px;
  /* border: none; */
  padding: 0.5rem 2rem;

  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.loginBtn:hover{
  color: #fff;
  /* padding: 0.5rem 4rem; */
  background-color: #062f70;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}



