.backwardsContainer {
  margin-top: 3rem;
  height: 280px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;

}

form{
  width: 80%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
}

.disTextfield {
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  resize: none;
  height: 200px;
  width: 100%;
  border: none;
  padding: 0.5rem;
}
.disInput {
  /* flex-basis: 100%; */
  width: 100px;
  height: 2rem;
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
  margin-bottom: 1rem;
  /* opacity: 0.8; */
  /* text-align: center; */
}



